@import 'utils/variables', 'utils/mixins';

.body {
  width: 924px;
  max-width: 100%;
}

.data_container {
  padding: 16px 24px 43px;
  gap: 24px;

  display: flex;
  align-items: flex-end;
  justify-content: space-between;

  max-height: 101px;
}

.span {
  margin-top: 10px;

  font-size: 17px;
  line-height: 18px;
  color: #0650a3;

  @include respond-to('medium') {
    font-size: 10px;
    margin-top: 15px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  margin-left: 10px;
  width: 180px;
  height: 45px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 110px;
    height: 30px;

    line-height: 20px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.data_button_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;

  padding-left: 34px;
  padding-right: 34px;
}

.data_medium {
  width: 360px;

  @include respond-to('large') {
    width: 310px;
  }

  @include respond-to('medium') {
    width: 240px;
  }
}

.data_medium span {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 10px;
  }
}

.radio {
  display: flex;
  flex-direction: row;
}

.radio_div {
  width: 50%;
}

.radio p {
  line-height: 38px;

  @include respond-to('medium') {
    line-height: 20px;
  }
}

.radio input[type='radio'] {
  display: none;
}

.radio label:before {
  content: ' ';
  display: inline-block;
  position: relative;
  top: 2px;
  margin: 0 10px 0 0;
  width: 16px;
  height: 16px;
  border-radius: 11px;
  border: 1px solid #4f99ee;
  background-color: transparent;

  @include respond-to('medium') {
    margin: 0 5px 0 0;
    width: 10px;
    height: 10px;
  }

  @include darkTheme {
    border-color: $moderate-blue;
  }
}

.cooperation_label:before {
  border: 1px solid #c4c4c4 !important;
}

.cooperation_label {
  color: #a8a8a8 !important;
}

.cooperation_label input[type='radio']:checked + span {
  background-color: #c4c4c4 !important;
}

.radio label {
  position: relative;

  font-size: 17px;
  color: #2a2f42;
  font-weight: 100;

  cursor: pointer;

  @include respond-to('medium') {
    font-size: 10px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.radio label input[type='radio']:checked + span {
  border-radius: 11px;
  width: 12.9px;
  height: 12.5px;
  position: absolute;
  top: 3.1px;
  left: 3.1px;
  display: block;
  background-color: #4f99ee;

  @include respond-to('large') {
    width: 13.5px;
    height: 13.5px;
    top: 1.4px;
    left: 2.3px;
  }

  @include respond-to('medium') {
    width: 8.5px;
    height: 8.5px;
    top: 0.7px;
    left: 1.9px;
  }

  @include darkTheme {
    background: $moderate-blue;
  }
}

.radio_div p:not(:first-child) {
  margin-left: 20px;
}

.show_all p:nth-child(n + 2) {
  margin-left: 0px !important;
}

.input_others {
  margin-left: 42px;
  outline: none;
  border: none;
  border-bottom: 1px solid #d2e8ff;
  width: 250px;
  color: #2a2f42;
  font-size: 17px;
  margin-top: 15px;
  padding-left: 3px;
  background-color: transparent;
  margin-bottom: 15px;

  &::selection {
    background-color: #d2e8ff;
  }

  @include respond-to('medium') {
    margin-left: 36px;
    width: 150px;
    font-size: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  @include darkTheme {
    color: $white;
  }
}
.input_others:disabled {
  color: #a8a8a8 !important;
  border-bottom: 1px solid #c4c4c4 !important;
}

.show_all {
  margin-top: 15px;

  @include respond-to('medium') {
    margin-top: 7px;
  }
}

.loader {
  margin-top: 50px;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.data_container input:disabled {
  background-color: #efefef;
  color: #adadad;

  @include darkTheme {
    background: $black-grey-light;
    color: $grey;
  }
}

.radio {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  line-height: 40px;

  cursor: pointer;

  span {
    margin: 0 5px;
    color: #45474b;
    font-size: 20px;
    font-family: $roboto;

    @include respond-to('large') {
      font-size: 17px;
    }

    @include respond-to('medium') {
      font-size: 11px;
    }

    @include darkTheme {
      color: $white;
    }
  }

  svg {
    color: #4f99ee;

    @include respond-to('medium') {
      width: 10px;
      height: 10px;
    }

    @include darkTheme {
      color: $moderate-blue;

      circle:nth-child(2) {
        fill: $black-blue;
      }
    }
  }

  @include respond-to('large') {
    line-height: 35px;
  }

  @include respond-to('medium') {
    line-height: 25px;
  }
}

.radioConsistency,
.containerConsistency {
  margin-left: 0 !important;
}

.spanTooltip {
  font-size: 17px;

  @include respond-to('large') {
    font-size: 12px;
  }

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.headerFixed {
  top: 0;
  background-color: white;

  @include darkTheme {
    background: $black-blue;
  }
}

.separateLine {
  border-bottom: 1px solid $base-gray;
}

.containerMainInfo {
  display: flex;
  flex-direction: row;

  padding: 16px 24px;
}

.lineIcon {
  min-width: 28px;
  width: 28px;
  height: 105px;

  margin-right: 8px;
}

.containerNameINN {
  display: flex;
  flex-direction: column;

  max-width: 95%;
}

.headerNameINN {
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;

  color: $base-black;

  &:not(:first-child) {
    margin-top: 25px;
  }
}

.detailsNameINN {
  margin-top: 4px;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  & > span {
    color: $base-middle-gray;

    display: flex;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    & > span {
      &:first-child {
        margin-right: 16px;
      }
    }

    &:first-child {
      margin-right: 32px;
      min-width: 150px;
    }
  }
}

.inn,
.name {
  color: $base-black !important;

  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown {
  height: 40px;

  margin-right: 24px;
  margin-top: 4px;
}

.dropdownSub {
  height: 40px;
}

.dropdownContainer {
  border-radius: 4px;

  &.error {
    border: 1px solid #ff6161 !important;
  }
}

.dropdownValue {
  color: rgb(84, 84, 84);

  @include darkTheme {
    color: $white;
  }
}

.dropdownLabel {
  font-weight: 700;
}

.dataHeader {
  margin-top: 30px;

  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  color: $base-black;
  padding: 0 20px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;

  @include respond-to('medium') {
    margin-top: 20px;

    font-size: 13px;
    line-height: 15px;

    &:last-child {
      margin-top: 30px;
    }
  }

  @include darkTheme {
    color: $grey;
  }
}

.dropdownItem {
  font-size: 15px;
}
